import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";

export default class Tutorial extends Component {
  constructor(props) {
    super(props);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.getTutorial = this.getTutorial.bind(this);
    this.updatePublished = this.updatePublished.bind(this);
    this.updateTutorial = this.updateTutorial.bind(this);
    this.deleteTutorial = this.deleteTutorial.bind(this);

    this.state = {
      currentTutorial: {
        id: null,
        application: "",
        profile: "",
        label: "",
        key: "",
        value: ""
      },
      message: ""
    };
  }

  componentDidMount() {
    this.getTutorial(this.props.match.params.id);
  }

  onChangeValue(key, value) {
    this.setState(function(prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          [key]: value
        }
      };
    });
  }


  onChangeTitle(e) {
    const title = e.target.value;

    this.setState(function(prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          title: title
        }
      };
    });
  }

  onChangeDescription(e) {
    const description = e.target.value;
    
    this.setState(prevState => ({
      currentTutorial: {
        ...prevState.currentTutorial,
        description: description
      }
    }));
  }

  getTutorial(id) {
    TutorialDataService.get(id)
      .then(response => {
        this.setState({
          currentTutorial: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  updatePublished(status) {
    var data = {
      id: this.state.currentTutorial.id,
      title: this.state.currentTutorial.title,
      description: this.state.currentTutorial.description,
      published: status
    };

    TutorialDataService.update(this.state.currentTutorial.id, data)
      .then(response => {
        this.setState(prevState => ({
          currentTutorial: {
            ...prevState.currentTutorial,
            published: status
          }
        }));
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  updateTutorial() {
    let updateTutorial = this.state.currentTutorial;
    updateTutorial.id = this.props.match.params.id
    TutorialDataService.update(
      updateTutorial
    )
      .then(response => {
        console.log(response.data);
        this.setState({
          message: "The configuration was updated. Check the env now."
        });
        this.props.history.push('/')
      })
      .catch(e => {
        console.log(e);
      });
  }

  deleteTutorial() {    
    TutorialDataService.delete(this.state.currentTutorial.id)
      .then(response => {
        console.log(response.data);
        this.props.history.push('/')
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    const { currentTutorial } = this.state;

    return (
      <div>
        {currentTutorial ? (
          <div className="edit-form">
            <h4>Property</h4>
            <form>
            <div className="form-group">
                <label htmlFor="title">Application</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentTutorial.application}
                  onChange={(e) => this.onChangeValue('application', e.target.value) }
                />
              </div>
              <div className="form-group">
                <label htmlFor="title">Profile</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentTutorial.profile}
                  onChange={(e) => this.onChangeValue('profile', e.target.value) }
                />
              </div>
              <div className="form-group">
                <label htmlFor="title">Label</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentTutorial.label}
                  onChange={(e) => this.onChangeValue('label', e.target.value) }
                />
              </div>
              <div className="form-group">
                <label htmlFor="title">Key</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentTutorial.key}
                  onChange={(e) => this.onChangeValue('key', e.target.value) }
                />
              </div>
              <div className="form-group">
                <label htmlFor="title">Value</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentTutorial.value}
                  onChange={(e) => this.onChangeValue('value', e.target.value) }
                />
              </div>
            </form>
            <button
              className="badge badge-danger mr-2"
              onClick={this.deleteTutorial}
            >
              Delete
            </button>

            <button
              type="submit"
              className="badge badge-success"
              onClick={this.updateTutorial}
            >
              Update
            </button>
            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please select a Property to edit...</p>
          </div>
        )}
      </div>
    );
  }
}
