import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";
import { Link } from "react-router-dom";
import { Table } from "reactstrap";

export default class TutorialsList extends Component {
  constructor(props) {
    super(props);
    this.onChangeSearchTitle = this.onChangeSearchTitle.bind(this);
    this.retrieveTutorials = this.retrieveTutorials.bind(this);
    this.refreshList = this.refreshList.bind(this);
    this.setActiveTutorial = this.setActiveTutorial.bind(this);
    this.removeAllTutorials = this.removeAllTutorials.bind(this);
    this.searchTitle = this.searchTitle.bind(this);

    this.state = {
      tutorials: [],
      currentTutorial: null,
      currentIndex: -1,
      searchTitle: "",
      user: {}
    };
  }

  componentDidMount() {
    this.retrieveTutorials();
    let user = JSON.parse(localStorage.getItem("user"));
    this.setState( { user: JSON.parse(localStorage.getItem("user")) });
    this.setState( { editorRole: (JSON.parse(localStorage.getItem("user")).roles.includes("ROLE_MODERATOR")) || (JSON.parse(localStorage.getItem("user")).roles.includes("ROLE_ADMIN"))}) 
  }

  onChangeSearchTitle(e) {
    const searchTitle = e.target.value;

    this.setState({
      searchTitle: searchTitle
    });
  }

  retrieveTutorials() {
    TutorialDataService.getAll()
      .then(response => {
        this.setState({
          tutorials: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  refreshList() {
    this.retrieveTutorials();
    this.setState({
      currentTutorial: null,
      currentIndex: -1
    });
  }

  setActiveTutorial(tutorial, index) {
    this.setState({
      currentTutorial: tutorial,
      currentIndex: index
    });
  }

  removeAllTutorials() {
    TutorialDataService.deleteAll()
      .then(response => {
        console.log(response.data);
        this.refreshList();
      })
      .catch(e => {
        console.log(e);
      });
  }

  searchTitle() {
    this.setState({
      currentTutorial: null,
      currentIndex: -1
    });

    TutorialDataService.findByTitle(this.state.searchTitle)
      .then(response => {
        this.setState({
          tutorials: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    const { searchTitle, tutorials, currentTutorial, currentIndex } = this.state;

    return (
      <div className="list row">
        <div className="col-md-8">
          <div className="input-group mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Search by title"
              value={searchTitle}
              onChange={this.onChangeSearchTitle}
            />
            <div className="input-group-append">
              <button
                className="btn btn-outline-secondary"
                type="button"
              >
                Search
              </button>
              {this.state.editorRole &&
                <button style={{ marginLeft: "20px"}} onClick={() => this.props.history.push('/add')} className="btn btn-success">
                  New Property
                </button>
                }
            </div>
          </div>
        </div>
        <div className="col-md-8">
          <h4>Property List</h4>
          <ul className="list-group">
            {tutorials &&
              tutorials.filter(x => !searchTitle ? true : x.key.toLowerCase().includes(searchTitle.toLowerCase())).map((tutorial, index) => (
                <li
                  className={
                    "list-group-item " +
                    (index === currentIndex ? "active" : "")
                  }
                  onClick={() => this.setActiveTutorial(tutorial, index)}
                  key={index}
                >
                  <b>{tutorial.key}</b><br/>{tutorial.value}
                </li>
              ))}
          </ul>
        </div>
        <div className="col-md-2">
          {currentTutorial ? (
            <div>
              <h4>Property</h4>
              <div>
                <label>
                  <strong>Application:</strong>
                </label>{" "}
                {currentTutorial.application}
              </div>
              <div>
                <label>
                  <strong>Profile:</strong>
                </label>{" "}
                {currentTutorial.profile}
              </div>
              <div>
                <label>
                  <strong>Label:</strong>
                </label>{" "}
                {currentTutorial.label}
              </div>
              <div>
                <label>
                  <strong>Key:</strong>
                </label>{" "}
                {currentTutorial.key}
              </div>
              <div>
                <label>
                  <strong>Value:</strong>
                </label>{" "}
                {currentTutorial.value}
              </div>
              {this.state.editorRole &&
              <Link
                to={"/tutorials/" + currentTutorial.id}
                className="badge badge-warning"
              >
                Edit
              </Link>}
            </div>
          ) : (
            <div>
              <br />
              <p>Please select a Property to edit...</p>
            </div>
          )}
        </div>
      </div>
    );
  }
}
