import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";

export default class AddTutorial extends Component {
  constructor(props) {
    super(props);
    this.save = this.save.bind(this);
    this.newTutorial = this.newTutorial.bind(this);

    this.state = {
      id: null,
      application: "ZOOM",
      profile: "DEV",
      label: "DB",
      key: "db_source",
      value: "jdbc"
    };
  }

  onChangeValue(key, value) {
    this.setState(function(prevState) {
      return {        
          ...prevState,
          [key]: value
      };
    });
  }

  save() {
    TutorialDataService.create(this.state)
      .then(response => {
        this.setState({
          id: response.data.id,
          title: response.data.title,
          description: response.data.description,
          published: response.data.published,

          submitted: true
        });
        this.props.history.push('/');
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  newTutorial() {
    this.setState({
      id: null,
      title: "",
      description: "",
      published: false,

      submitted: false
    });
  }

  render() {
    return (
      <div className="submit-form">
        {this.state.submitted ? (
          <div>
            <h4>The property added. </h4>
            <button className="btn btn-success" onClick={this.newTutorial}>
              Add a new one
            </button>
          </div>
        ) : (
          <div>
            <div className="form-group">
              <label htmlFor="title">Application</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.application}
                onChange={(e) => this.onChangeValue('application', e.target.value)}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">Profile</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.profile}
                onChange={(e) => this.onChangeValue('profile', e.target.value)}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">Label</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.label}
                onChange={(e) => this.onChangeValue('label', e.target.value)}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">Key</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.key}
                onChange={(e) => this.onChangeValue('key', e.target.value)}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">Value</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.value}
                onChange={(e) => this.onChangeValue('value', e.target.value)}
                name="title"
              />
            </div>
            <button onClick={this.save} className="btn btn-success">
              Submit
            </button>
          </div>
        )}
      </div>
    );
  }
}
