import http from "../http-common";
import axios from "axios";
import authHeader from "./auth-header";

//const API_URL = "http://localhost:8082/api/config";
const API_URL = process.env.REACT_APP_SERVER_URL;
const CONFIG_PREFIX = "api/config";
class TutorialDataService {

  getAudit() {
    return axios.get(`${API_URL}/${CONFIG_PREFIX}/audit`, { headers: authHeader() });
  }
  
  getAll() {
    return axios.get(`${API_URL}/${CONFIG_PREFIX}`, { headers: authHeader() });
  }

  get(id) {
    return axios.get(`${API_URL}/${CONFIG_PREFIX}/${id}`, { headers: authHeader() });
  }

  create(data) {
    return axios.post(`${API_URL}/${CONFIG_PREFIX}`, data, { headers: authHeader() });
  }

  update(data) {
    return axios.put(`${API_URL}/${CONFIG_PREFIX}`, data, { headers: authHeader() });
  }

  delete(id) {
    return axios.delete(API_URL, {
      headers: authHeader(),
      data: { id: id }
    });
  };
}

export default new TutorialDataService();